$mainFont: "Montserrat";

$backgroundColor: #1e1e1e;
$cardBackground: #373737;

@mixin mainColor {
  background-image: linear-gradient(#aa9053, #e9d796, #f8e29f, #b8984d);
  background-clip: text;
  color: transparent;
}
$goldBg: linear-gradient(-205deg, #aa9053, #e0cf90, #f8e29f, #e0cf90, #b8984d);

$textColor: #7d786a;
$mainText: #fcedc2;

$borderColor: #424138;

$selectedColor: #313131;
